@-webkit-keyframes tfw-view-icon-rotation {
    from {
        -webkit-transform: rotate(0);
        transform: rotate(0)
    }

    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg)
    }
}

@keyframes tfw-view-icon-rotation {
    from {
        -webkit-transform: rotate(0);
        transform: rotate(0)
    }

    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg)
    }
}

.tfw-view-icon {
    display: inline-block;
    -webkit-transition: all .3s;
    transition: all .3s;
    margin: 2px;
    padding: 0;
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}

.tfw-view-icon.zero {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
}

.tfw-view-icon.active {
    cursor: pointer;
}

.tfw-view-icon.animate {
    animation: tfw-view-icon-rotation 1.1s linear infinite;
    -webkit-animation: tfw-view-icon-rotation 1.1s linear infinite;
}
div.tfw-view-flex {
  display: inline-flex;
  margin : 2px;
}
div.tfw-view-flex.wide {
  display: flex;
  width  : 100%;
  margin : 0;
}
div.tfw-view-flex.dir-row {
  flex-direction: row;
}
div.tfw-view-flex.dir-row-reverse {
  flex-direction: row-reverse;
}
div.tfw-view-flex.dir-column {
  flex-direction: column;
}
div.tfw-view-flex.dir-column-reverse {
  flex-direction: column-reverse;
}
@media(orientation:landscape) {
  div.tfw-view-flex.dir-wide {
    flex-direction: row;
  }
  div.tfw-view-flex.dir-narrow {
    flex-direction: column;
  }
}
@media(orientation:portrait) {
  div.tfw-view-flex.dir-wide {
    flex-direction: column;
  }
  div.tfw-view-flex.dir-narrow {
    flex-direction: row;
  }
}
